import mainpageleft from '@/components/mainpageleft.vue';
import searchpage from '../pages/search/search';
export default {
	props: {
		// 渲染时候替换默认值会被替换
		userInfo: {
			faceImg: '../static/touxiang.jpg'
		},
		topArticles: '',
		topHeight: ''
	},
	components: {
		mainpageleft,
		searchpage
	},

	data() {
		return {
			showMainPageLeft: 0,
			showSearch: 0
		};
	},

	methods: {
		controlShowLeft(a) {
			this.showMainPageLeft = a;
			// console.log(this.showMainPageLeft);
		},
		controlShowSearch(a) {
			console.log('this is controlShowSearch, receive data will be appied');
			console.log(a);
			this.showSearch = a;
		},
		jumpToSubmit() {
			uni.navigateTo({
				url: '/pages/submit/submit'
			});
		},
		goToDetail(article) {
			uni.navigateTo({
				url: '/pages/detail/detail?data=' + JSON.stringify(article)
			});
		}
	}
};
